
/* -- GLOBALS -- */

* { 
  /*font-family: Verdana;*/
  text-align: center;
}

*:focus {
  outline: 2.5px inset steelblue;
  -moz-outline-radius: 10px;
}

/* -- CONTAINERS -- */

.Flex-container {
  text-align: center;
  display: flex;
  flex-direction: column;
}

.Flex-container-row {
  text-align: center;
  display: flex;
  flex-direction: row;
}

/* -- TEXT -- */

p:hover {
  transform: scaleX( 2.5 )
}

#bigText {
  font-size: 40px;
  font-family: Impact;
  font-style: oblique;
  color: black;
  background-color: aqua;
}

#preText {
  font-size: 18px;
  padding: 20px;
}

#papyrus {
  font-weight: 900;
  opacity: 0.6;
}

/* * * -- INPUTS -- * * */

button:hover {
  transform: scale( 1.5 );
}

button:active {
  animation: spin 0.2s linear;
}

button {
  font-size: 25px;
  padding: 20px;
  width: 200px;
  margin-top: 10px;
  margin-bottom: 10px;
  border-style: outset;
  border-width: 7px;
  border-radius: 30px;
}

button.YYY {
  border-color: #379000;
  color: #123dfb;
  background: #a2ff96;
  animation: pulse infinite 1s linear alternate;
}

button.more {
  color: #623afb;
  background: #f8cf66;
  animation: rainbow infinite 1s linear alternate-reverse;
}

button.tellMore {
  background-color: black;
  color: white;
}

/* -- MEDIA -- */

img[alt="logo"] {
  animation: spin infinite 20s linear alternate-reverse;
}

img[alt="cards"] {
  animation: awkward-slide 5s infinite alternate; 
}

img:hover {
  transform: skewX( 90deg );
}

/* -- MISC -- */

footer {
  margin-top: 20px;
  animation: slide infinite 3s linear;
}

header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.flashBG {
  animation: flash-bg 100ms linear infinite alternate;
}

.flashFG {
  animation: flash-fg 100ms linear infinite alternate;
}

.orbit {
  animation: orbit 20s linear infinite;
}

/* -- ANIMATIONS -- */

@keyframes rainbow {
  0% {
    background-color: red;
    color: green;
  }
  25% {
    background-color: yellow;
    color: red;
  }
  50% {
    background-color: blue;
    color: yellow;
  }
  100% {
    background-color: green;
    color: red;
  }
}

@keyframes pulse {
  from { transform: scale( 0.5 ); }
  to { transform: scale( 1.5 ); }
}

@keyframes orbit {
  from { transform: rotate( 0deg ) translateX( 250px ) rotate( 0deg ); }
  to { transform: rotate( 360deg ) translateX( 250px ) rotate( -360deg ); }
}

@keyframes orbit-rev {
  from { transform: rotate(360deg) translateX(25px) rotate(-360deg); }
  to { transform: rotate(0deg) translateX(25px) rotate(0deg); }
}

@keyframes awkward-slide {
  0% { transform: translateX(-200px) }
  25% { transform: translateX(-100px) }
  50% { transform: translateX(100px) }
  75% { transform: translateX(-100px) }
  100% { transform: translateX(-200px) }
}

@keyframes flash-bw {
  from {
    color: white;
    background-color: black;
  }
  to {
    color: black;
    background-color: white;
  }
}

@keyframes flash-fg {
  from { color: black; }
  to { color: blue; }
}

@keyframes flash-bg {
  from { background-color: aqua; }
  to { background-color: wheat; }
}

@keyframes spin {
  from {
    transform: rotate( 0deg );
  }
  to {
    transform: rotate( 360deg );
  }
}
